import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import { convertDiscountMask, getFileName } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({ id: "itemCode" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name"
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "baseQuantity" }),
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "tax" }),
        dataIndex: "tax.tax_name"
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        align: "right",
        render: text => convertDiscountMask(text)
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="purchaseInvoices.dueDate" />,
        description: data.due_date
          ? moment(data.due_date, "YYYY-MM-DD HH:mm:ss").format("DD MMMM YYYY")
          : null
      },
      {
        label: <IntlMessages id="purchaseInvoices.location" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="purchaseInvoices.paymentCategory" />,
        description: data.payment_category
          ? data.payment_category.payment_category_code
          : null
      },
      {
        label: <IntlMessages id="purchaseInvoices.paymentTerm" />,
        description: data.payment_term
          ? data.payment_term.payment_term_code
          : null
      },
      {
        label: <IntlMessages id="salesman" />,
        description: data.salesman_name ? data.salesman_name : "-"
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="salesOrders.customerPOInfo.no" />,
        description: data.customer_po_no ? data.customer_po_no : "-"
      },
      {
        label: <IntlMessages id="salesOrders.customerPOInfo.date" />,
        description: data.customer_po_date
          ? moment(data.customer_po_date, "YYYY-MM-DD").format("DD MMMM YYYY")
          : "-"
      },
      {
        label: <IntlMessages id="contactPerson" />,
        description:
          data.phone && data.contact_person
            ? data.phone + " (" + data.contact_person + ")"
            : "-"
      },
      {
        label: <IntlMessages id="salesOrders.shipDate" />,
        description: data.ship_date
          ? moment(data.ship_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY, HH:mm:ss"
            )
          : "-"
      },
      {
        label: <IntlMessages id="purchaseOrders.shipTo" />,
        description: data.ship_to ? data.ship_to : "-"
      }
    ];

    const total = [
      {
        label: <IntlMessages id="purchaseOrders.exlDiscountAndTax" />,
        amount: <NumberFormat value={data.total_exclude_discount_tax} />
        // parseAmount: true
      },
      {
        isDiscount: true,
        label: <IntlMessages id="purchaseOrders.discount" />,
        discount: data.discount,
        amount: <NumberFormat value={data.discount_amount} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="purchaseOrders.totalDiscount" />,
        amount: <NumberFormat value={data.total_discount} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="purchaseOrders.totalTax" />,
        amount: <NumberFormat value={data.total_tax} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="total" />,
        amount: <NumberFormat value={data.total_amount} />
        // parseAmount: true
      }
      // {
      //   label: <IntlMessages id="purchaseOrders.downPayment" />,
      //   amount: data.total_down_payment,
      //   parseAmount: true
      // }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.salesOrder" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.sales_order_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.customer" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.customer ? data.customer.customer_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.sales_order_details}
                rowKey={row => row.id_sales_order_detail}
                pagination={false}
              />
            </Col>
            <Col span={12}>
              <Col span={15} style={{ marginTop: "15px", textAlign: "left" }}>
                <strong style={{ fontSize: "12px" }}>
                  <IntlMessages id="purchaseInvoices.totalQty" />
                </strong>
                <strong style={{ fontSize: "12px" }}>
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "15px" }}>
                <strong style={{ fontSize: "12px" }}>
                  <NumberFormat value={data.total_qty} />
                </strong>
              </Col>
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "sales_order",
              data.sales_order_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
