import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SalesOrderUpdateForm from "./updateForm";
// import HistoryPurchaseOrder from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import Button from "../../../../components/uielements/button";
import HistorySalesOrder from "./history";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Print from "./print";
import actions from "../../../../redux/sales/salesOrder/actions";
import { Box } from "../../../App/app.style";

const redirecTo = "/dashboard/sales/sales-orders";

class UpdateSalesOrder extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editSalesOrder } = this.props;
    const { id } = this.props.match.params;
    editSalesOrder(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesOrder.error) {
      notification("error", this.props.salesOrder.error);
      this.props.clearError();
    }
    if (this.props.salesOrder.success) {
      notification("success", this.props.salesOrder.success);
      this.props.clearSuccess();
    }
    if (this.props.salesOrder.warning) {
      notification("warning", this.props.salesOrder.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateSalesOrder } = this.props;
    const { id } = this.props.match.params;
    updateSalesOrder(id, value);
  };

  handleSubmitCancelSO = value => {
    const { cancelSalesOrder } = this.props;
    const { id } = this.props.match.params;

    cancelSalesOrder(id, value);
  };

  handleSubmitUnconfirmSO = value => {
    const { unconfirmSalesOrder } = this.props;
    const { id } = this.props.match.params;

    unconfirmSalesOrder(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit, salesOrder } = this.props.salesOrder;
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="salesOrders.update.title" />}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={salesOrder} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <HistorySalesOrder
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={salesOrder ? salesOrder.description : null}
          />

          {loading && !loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <SalesOrderUpdateForm
              handleSubmitCancelSO={this.handleSubmitCancelSO}
              handleSubmitUnconfirmSO={this.handleSubmitUnconfirmSO}
              salesOrder={this.props.salesOrder}
              submit={this.handleSubmit}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesOrder: state.salesOrder,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSalesOrder);
