import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import { Col, Modal, Tooltip, Table, Row } from "antd";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

class ModalPriceCompare extends Component {
  state = {};

  render() {
    const { data, visible } = this.props;

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%"
        // fixed: "left"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "10%",
        // fixed: "left",
        render: text => {
          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        width: "10%",
        align: "right",
        editable: false,
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "updatedPrice" }),
        dataIndex: "item_price_updated",
        width: "10%",
        align: "right",
        editable: false,
        render: text => <NumberFormat value={text} />
      }
    ];

    let dataSource = [];
    const { lastUpdatedTransDate, transaction_date } = data ? data : [];
    if (data && data.sales_order_details) {
      dataSource = [...this.props.data.sales_order_details];
    }
    return (
      <Modal
        centered
        closable={false}
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              <Button
                type="primary"
                onClick={this.props.submitPrev}
              >
                <IntlMessages id="salesOrders.comparePrice.prevPrice"></IntlMessages>
              </Button>

              <Button
                type="primary"
                onClick={() => this.props.updateSalesPrice(lastUpdatedTransDate)}
              >
                <IntlMessages id="salesOrders.comparePrice.newPrice"></IntlMessages>
              </Button>
            </Col>
          </Row>
        }
        title={<IntlMessages id="salesOrders.comparePrice.title" />}
        visible={visible}
        maskClosable={false}
        onCancel={() => console.log("ke cancel")}
        width="80%"
      >
        <Row
          type="flex"
          justify="start"
          style={{ alignItems: "center", marginBottom: "6px" }}
        >
          <Col span={6} style={{ marginTop: "5px" }}>
              <IntlMessages id="transactionDate.current" />
          </Col>
          <Col span={8} offset={1}>
            <span>{transaction_date}</span>
          </Col>
        </Row>
        <Row
          type="flex"
          justify="start"
          style={{ alignItems: "center", marginBottom: "6px" }}
        >
          <Col span={6} style={{ marginTop: "5px" }}>
            <strong>
              <IntlMessages id="transactionDate.lastUpdate" />
            </strong>
          </Col>
          <Col span={8} offset={1}>
            <span>{lastUpdatedTransDate}</span>
          </Col>
        </Row>
        <Table
          dataSource={dataSource}
          columns={columns}
          // loading={loadingDeliveryOrders}
          // Set pagination, jika ada page_size_do mengunakan pagination API
          // jika tidak ada page_size_do menggunakan pagination bawaan
        />
      </Modal>
    );
  }
}

export default ModalPriceCompare;
