import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Col, Upload, Tooltip, Icon } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import actions from "../../../../redux/sales/salesOrder/actions";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

class SalesOrderDocUpdateForm extends Component {
  state = {
    fileList: [],
    //file : null,
    uploading: false
  };

  componentDidUpdate() {
    if (this.props.salesOrder.success) {
      this.setState({
        uploading: false,
        fileList: []
      });
    }
  }

  handleUpload = () => {
    const { fileList } = this.state;
    const { uploadDocumentSO } = this.props;
    let body;
    fileList.forEach(file => {
      // console.log(file);
      body = {
        document: file,
        description:
          this.props.form.getFieldValue("description") === undefined
            ? null
            : this.props.form.getFieldValue("description")
      };
    });

    // console.log(body)
    this.setState({
      uploading: true
    });

    uploadDocumentSO(this.props.salesOrder.salesOrder.id_sales_order, body);
  };

  handleDelete = id_doc => {
    const { deleteDocumentSO } = this.props;
    const id = this.props.salesOrder.salesOrder.id_sales_order;
    deleteDocumentSO(id_doc, id);
  };

  listAction = {
    title: "",
    dataIndex: "id_sales_order_document",
    rowKey: "id_sales_order_document",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip title={<IntlMessages id="download" />}>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href={
              localStorage.getItem("server") + row.document_path
            }
            download
          >
            <Button className="invoiceRstBtn" size="small">
              <i className="ion-android-download" />
            </Button>
          </a>
        </Tooltip>

        <Popconfirms
          title={<IntlMessages id="document.popconfirm.delete" />}
          onConfirm={this.handleDelete.bind(this, text)}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
        >
          <Tooltip title={<IntlMessages id="delete" />}>
            <Button className="invoiceDltBtn" size="small">
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    // console.log(this.props.salesOrder.loading);
    const { getFieldDecorator } = this.props.form;
    const { salesOrder } = this.props;
    const documents = salesOrder.salesOrder
      ? salesOrder.salesOrder.documents
        ? salesOrder.salesOrder.documents
        : []
      : [];
    const { loading } = salesOrder;

    const columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "uploaded_date",
        rowKey: "uploaded_date",
        width: "25%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "fileDescription" }),
        dataIndex: "document_path",
        rowKey: "document_path",
        width: "50%",
        render: (text, row) => {
          return (
            // <a href={localStorage.getItem('server') + text} download>
            row.description ? row.description : "-"
            // </a>
          );
        }
      },
      {
        title: getLanguage({ id: "uploadedBy" }),
        dataIndex: "uploaded_by",
        rowKey: "uploaded_by",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      }
    ];

    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { fileList } = this.state;
    const props = {
      multiple: false,
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList
          };
        });
      },
      beforeUpload: file => {
        this.setState({
          fileList: [file]
        });
        return false;
      },
      fileList
    };

    if (
      permissions.includes("Upload Doc Sales Order") &&
      columns.length === 3
    ) {
      columns.push(this.listAction);
    }

    Object.keys(documents).map((document, index) => {
      return dataSource.push({
        ...documents[document],
        key: document
      });
    });

    return (
      <div style={{ padding: "0 20px 0 20px" }}>
        {permissions.includes("Upload Doc Sales Order") ? (
          <React.Fragment>
            <Col span={8}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="fileDescription" />}
              >
                {getFieldDecorator("description", {
                  // initialValue: ,
                  rules: [
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(<Input style={{ width: "100%", height: "35px" }} />)}
              </Form.Item>
            </Col>
            <Col span={4} style={{ marginTop: "20px" }}>
              <Upload {...props}>
                <Button>
                  <Icon type="upload" /> <IntlMessages id="selectFile" />
                </Button>
              </Upload>
            </Col>
            <Button
              type="primary"
              onClick={this.handleUpload}
              disabled={fileList.length === 0}
              loading={loading}
              style={{ marginTop: "20px", marginLeft: "0%" }}
            >
              {loading ? (
                <IntlMessages id="uploading" />
              ) : (
                <IntlMessages id="upload" />
              )}
            </Button>
          </React.Fragment>
        ) : null}

        <Col span={24} style={{ marginTop: "20px" }}>
          <CardWrapper>
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  pagination={true}
                />
              </Scrollbars>
            </div>
          </CardWrapper>
        </Col>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesOrder: state.salesOrder,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  SalesOrderDocUpdateForm
);
