const TabPane = Tabs.TabPane;

const {
  cancelSalesOrder,
  closeSalesOrder,
  confirmSalesOrder,
  getCustomerContacts,
  getItemsForSO,
  getUpdateSODetails,
  unconfirmSalesOrder
} = actions;

class SalesOrderUpdateForm extends Component {
  state = {
    autoSaveTrans: false,
    activeCustDisc: null, // indicate which discount bill (discount_code) is being used
    calcDiscItem: false,
    clear: false,
    comparePriceData: {},
    customer: {},
    custDisc: "0", // dinamic discount bill
    cust_disc: "", // fixed disc per cust from cust foreign keys
    customerContactsName: [],
    discBill: null, //selected discount bill's detail    
    discount_amount: 0,
    discount_unmasked: null,
    exceededItems: [],
    notAvailableItems: {},
    exlcDiscTax: 0,
    freight_include_in_total: 0,
    fullDiscMultiplier: null, //printed of full discount string for discount multiplier
    id_customer: null,
    id_freight_account: null,
    id_location: null,
    id_salesman: null,
    inclusive_tax: false,
    initPayType: 1,
    // id_item_request: [],
    item: null,
    itemDetails: {
      sales_order_details: []
    },
    items: [],
    limit: 0,
    limit_first_credit: 0,

    // untuk checking perhitungan sudah selesai 
    // ketika user memilih update price
    // sehingga bisa di confirm
    limitCompareItemsDiscount: 0,

    netPayDay: null,
    netPayDisc: 0,
    restore: null,
    paymentCategory: {
      id_payment_category: null,
      payment_type: null
    },
    search: null,
    soDetails: {
      so_down_payment: {
        // amount: null,
        // bank_issuer: null,
        // dp_due_date: null,
        // id_cash_bank: null,
        // id_cash_flow_type: null,
        // id_dp_account: null,
        // id_purchase_order: null,
        // reference_no: null
      }
    },
    tab: "1",
    total_amount: 0,
    total_discount: 0,
    total_exclude_discount_tax: 0,
    sum_subtotal_discount_item: 0,
    total_freight_cost: 0,
    total_tax: 0,
    total_qty: 0,
    transDate: null,
    viewed: false,
    viewedInfoItemGrouping: false,
    visible: false,
    visibleItemReq: false,
    visibleDownPayment: false,
    visibleFreightCost: false,
    visibleModalCancelSO: false,
    visibleModalPriceCompare: false,
    visibleModalUnconfirmSO: false,
    calculatingDiscount: "none",
    dataChanged: false,
    err_discount: false,
    err_item: false,
  };

  componentDidMount = () => {
    const transDate = this.props.salesOrder
      ? this.props.salesOrder.salesOrder
        ? this.props.salesOrder.salesOrder.transaction_date
        : null
      : null;
    const transStatus = this.props.salesOrder
      ? this.props.salesOrder.salesOrder
        ? this.props.salesOrder.salesOrder.transaction_status
        : null
      : null;
    this.setState({ transDate, transStatus });

    // when ordered qty is exceeded 
    if (this.props.salesOrder.info) {
      const { info } = this.props.salesOrder
      this.setState({
        exceededItems: info.exceeded_items,
        notAvailableItems: info.not_available_items
      })
    }
  };

  componentDidUpdate(prevProps, prevState) {
    const { loading, loadingForeign, paymentCategories, salesOrder } = this.props.salesOrder;
    const { getCustomerContacts, getItemsForSO } = this.props;
    const prevSalesOrder = prevProps.salesOrder.salesOrder;
    if (!loading && prevSalesOrder !== salesOrder) {
      if (salesOrder) {
        getItemsForSO(
          salesOrder.id_customer,
          salesOrder.id_location,
          salesOrder.transaction_date,
          salesOrder.id_payment_category
        );
        getCustomerContacts(salesOrder.id_customer);
      }
      const customerForContact = salesOrder.customer;
      if (customerForContact.customer_contacts.length > 0) {
        const customerFindContact = [];
        for (var i = 0; i < customerForContact.customer_contacts.length; i++) {
          customerFindContact.push(
            customerForContact.customer_contacts[i].customer_contact_name
          );
        }
        this.setState({
          customerContactsName: customerFindContact
        });
      } else {
        this.setState({ customerContactsName: [] });
      }

      // discount management adjustment
      // get cust discount from foreign keys
      const { customers } = this.props.salesOrder;
      const customer = customers.find(
        data => data.id_customer === salesOrder.id_customer
      );
      const cust_disc = customer && customer.discount
        ? parseFloat(customer.discount) * 100 + "%"
        : 0;
      const custDisc = salesOrder ? salesOrder.discount : "0%"
      let activeCustDisc = null
      if (salesOrder && salesOrder.discount_bill) {
        activeCustDisc = salesOrder.discount_bill.discount_code
      }
      const items = salesOrder ? salesOrder.sales_order_details : []
      const discount_amount = getSupplierDiscount(items, custDisc)
      // /discount management adjustment

      const paymentCategory = salesOrder ? paymentCategories.find(data => data.id_payment_category === salesOrder.id_payment_category) : null
      
      this.setState({
        activeCustDisc,
        custDisc,
        cust_disc,
        discount_amount,
        discountAmount: salesOrder ? salesOrder.discount_amount : 0,
        due_date: salesOrder
          ? moment(salesOrder.transDate).add(salesOrder.net_payment_day, "d")
          : null,
        id_location: salesOrder ? salesOrder.id_location : null,
        id_customer: salesOrder ? salesOrder.id_customer : null,
        inclusive_tax: salesOrder ? salesOrder.inclusive_tax : [],
        items,
        netPayDay: salesOrder ? salesOrder.net_payment_day : 0,
        netPayDisc: salesOrder ? salesOrder.payment_term.discount : 0,
        paymentCategory,
        soDetails: {
          so_down_payment: {
            id_sales_order: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_sales_order
                : null
              : null,
            id_cash_bank: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_cash_bank
                : null
              : null,
            id_dp_account: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_dp_account
                : null
              : null,
            id_cash_flow_type: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_cash_flow_type
                : null
              : null,
            bank_issuer: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.bank_issuer
                : null
              : null,
            reference_no: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.reference_no
                : null
              : null,
            dp_due_date: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.dp_due_date
                : null
              : null,
            amount: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.amount
                : null
              : null
          }
        },
        discount_unmasked: salesOrder ? salesOrder.discount : "0"
      });
      this.props.form.setFieldsValue({ 
        discount: salesOrder ? convertDiscountMask(salesOrder.discount) : "0"
      })

    } else if (
      !loading && !loadingForeign &&
      prevState.itemDetails.sales_order_details.length === 0 &&
      this.state.limit === 0 &&
      prevSalesOrder === salesOrder
    ) {
      // get items from idcustomer, idlocation, transaction date, payment
      if (salesOrder) {
        getItemsForSO(
          salesOrder.id_customer,
          salesOrder.id_location,
          salesOrder.transaction_date,
          salesOrder.id_payment_category
        );
        getCustomerContacts(salesOrder.id_customer);
      }

      // mapping customer contact
      const customerForContact = salesOrder.customer;
      if (customerForContact.customer_contacts.length > 0) {
        const customerFindContact = [];
        for (var i = 0; i < customerForContact.customer_contacts.length; i++) {
          customerFindContact.push(
            customerForContact.customer_contacts[i].customer_contact_name
          );
        }
        this.setState({
          customerContactsName: customerFindContact
          // customerContacts: customer.customer_contacts
        });
      } else {
        this.setState({ customerContactsName: [] });
      }

      // discount management adjustment
      // get cust discount from foreign keys
      // const { customers } = this.props.salesOrder;
      // const customer = customers.find(
      //   data => data.id_customer === salesOrder.id_customer
      // );
      const { customer } = salesOrder;
      const cust_disc = customer && customer.discount
        ? parseFloat(customer.discount) * 100 + "%"
        : 0;
      const custDisc = salesOrder ? salesOrder.discount: "0%"
      let activeCustDisc = null
      if(salesOrder && salesOrder.discount_bill) {
        activeCustDisc = salesOrder.discount_bill.discount_code
      }
      const items = salesOrder ? salesOrder.sales_order_details : []
      // const discount_amount = getSupplierDiscount(items, custDisc)
      // /discount management adjustment

      const paymentCategory = salesOrder ? paymentCategories.find(data => data.id_payment_category === salesOrder.id_payment_category) : null

      this.setState({
        activeCustDisc,
        custDisc,
        cust_disc,
        // discount_amount,
        discountAmount: salesOrder ? salesOrder.discount_amount : 0,
        freight_include_in_total: salesOrder ? salesOrder.freight_include_in_total : 0,
        due_date: salesOrder
          ? moment(salesOrder.transDate).add(salesOrder.net_payment_day, "d")
          : null,
        id_customer: salesOrder ? salesOrder.id_customer : null,
        customer: salesOrder ? salesOrder.customer : {},
        id_freight_account: salesOrder ? salesOrder.id_freight_account : null,
        id_location: salesOrder ? salesOrder.id_location : null,
        inclusive_tax: salesOrder ? salesOrder.inclusive_tax : [],
        items,
        limit: 1,
        netPayDay: salesOrder ? salesOrder.net_payment_day : 0,
        netPayDisc: salesOrder ? salesOrder.payment_term.discount : 0,
        paymentCategory,
        soDetails: {
          so_down_payment: {
            id_sales_order: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_sales_order
                : null
              : null,
            id_cash_bank: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_cash_bank
                : null
              : null,
            id_dp_account: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_dp_account
                : null
              : null,
            id_cash_flow_type: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.id_cash_flow_type
                : null
              : null,
            bank_issuer: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.bank_issuer
                : null
              : null,
            reference_no: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.reference_no
                : null
              : null,
            dp_due_date: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.dp_due_date
                : null
              : null,
            amount: salesOrder
              ? salesOrder.so_down_payment
                ? salesOrder.so_down_payment.amount
                : null
              : null
          }
        },
        total_freight_cost : salesOrder ? salesOrder.total_freight_cost : 0,
        transDate: salesOrder ? salesOrder.transaction_date : null,
        discount_unmasked: salesOrder ? salesOrder.discount : "0"
      });

      this.props.form.setFieldsValue({ 
        discount: salesOrder ? convertDiscountMask(salesOrder.discount) : "0"
      })
    }

    // handle payment categories-state initiate
    if(paymentCategories.length > 0 && this.state.initPayType === 1) {
      const paymentCategory = salesOrder ? paymentCategories.find(data => data.id_payment_category === salesOrder.id_payment_category) : null
      this.setState({
        initPayType: 0,
        paymentCategory
      })
    }

    // handle reset DP on change payment type
    // if (prevState.paymentCategory && this.state.paymentCategory && prevState.paymentCategory.payment_type !== this.state.paymentCategory.payment_type) {
    //   const { payment_type } = this.state.paymentCategory
    //   const { setFieldsValue } = this.props.form
    //   if (payment_type !== 2) {
    //     this.setState({
    //       soDetails: {
    //         so_down_payment: {}
    //       }
    //     })
    //     setFieldsValue({
    //       amount: 0
    //     })
    //   }
    // }

    /* Ubah item list setelah dapat discount */
    // if (
    //   prevProps.salesOrder.itemDiscount !== this.props.salesOrder.itemDiscount
    // ) {
    //   this.setState({
    //     items: this.props.salesOrder.itemDiscount,
    //     clear: this.props.salesOrder.itemDiscount.length === 0 ? true : false
    //   });
    // }

    /* Get Discount setelah klik import */
    // if (
    //   salesOrder.sales_order_details.length > 0 &&
    //   salesOrder.sales_order_details !==
    //     this.state.itemDetails.sales_order_details &&
    //   this.state.id_supplier &&
    //   !prevState.visibleItemReq &&
    //   !this.state.visibleItemReq &&
    //   prevState.items.length > 0 &&
    //   this.state.itemDetails.sales_order_details.length > 0 &&
    //   (prevProps.salesOrder.itemDiscount !==
    //     this.props.salesOrder.itemDiscount ||
    //     this.props.salesOrder.itemDiscount.length === 0)
    // ) {
    //   this.getDiscountItemList();
    // }

    // set field value untuk customer credit limit
    const { id_customer, limit_first_credit } = this.state;
    const { customers } = this.props.salesOrder;
    const { setFieldsValue } = this.props.form;
    if (id_customer && limit_first_credit === 0 && customers.length > 0) {
      const customer = customers.find(
        data => data.id_customer === id_customer
      );
      setFieldsValue({
        customer_credit_limit: customer && customer.credit_limit
          ? parseFloat(customer.credit_limit).toFixed(2).replace(
            /\B(?=(\d{3})+(?!\d))/g,
            ","
          ) : null,
      })
      this.setState({ limit_first_credit: 1 })
    }

    // onConfirm transaction
    if (
      this.props.salesOrder.update_sales_order_details !== 
      prevProps.salesOrder.update_sales_order_details && 
      !this.props.salesOrder.updateItems
    ) {
      const { getFieldValue } = this.props.form
      const { itemDetails } = this.state
      let { sales_order_details } = itemDetails
      const { update_sales_order_details } = this.props.salesOrder
      let foundDifferentPrice = false
      for(var i=0;i<sales_order_details.length;i++){
        const findUpdate = update_sales_order_details.find(update => update.id_item === sales_order_details[i].id_item)
        if(findUpdate && parseFloat(findUpdate.item_price) !== parseFloat(sales_order_details[i].item_price)) {
          foundDifferentPrice = true
          break;
        }
      }
      if(foundDifferentPrice){
        let setStateData = {
          comparePriceData: {}
        };

        sales_order_details.map((data, index) => {
          const findUpdate = update_sales_order_details.find(update => update.id_item === sales_order_details[index].id_item)
          if(findUpdate) {
            data.item_price_updated = findUpdate.item_price 
          } else {
            data.item_price_updated = data.item_price
          }
        })
        
        setStateData.comparePriceData.sales_order_details = sales_order_details

        setStateData.comparePriceData.transaction_date = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
        setStateData.comparePriceData.lastUpdatedTransDate = this.props.salesOrder.SOTransactionDate
        setStateData.comparePriceData.discount_bill = this.props.salesOrder.SODiscountBill

        setStateData.visibleModalPriceCompare = true
        this.setState(setStateData)
      } else {
        this.confirmInvoiceAfterCompare()
      }
    }

    // important to check "true"
    if (this.state.calculatingDiscount === true) {
      // console.log(
      //   prevState.itemDetails.sales_order_details === this.state.itemDetails.sales_order_details,
      //   prevState.total_discount === this.state.total_discount,
      //   this.state.limitCompareItemsDiscount
      // )
      // jika limit compare sudah lebih dari 0 maka siap dikirim
      if (prevState.itemDetails.sales_order_details === 
        this.state.itemDetails.sales_order_details &&
        this.state.limitCompareItemsDiscount > 0
      ) {
        this.confirmInvoiceAfterCompare();
        this.setState({ calculatingDiscount: false, limitCompareItemsDiscount: 0 });
      } 
      // jika true, true, count bertambah
      else if (
        prevState.itemDetails.sales_order_details === this.state.itemDetails.sales_order_details &&
        prevState.total_discount === this.state.total_discount
      ) {
        const { limitCompareItemsDiscount } = this.state;
        this.setState({ limitCompareItemsDiscount: limitCompareItemsDiscount + 1 });
      }
    }

    /* Get Discount Bill*/
    if (
      this.props.salesOrder &&
      prevProps.salesOrder.discountBills !==
      this.props.salesOrder.discountBills
    ) {
      let setStateData = {}
      const { discountBills } = this.props.salesOrder;
      const { discount_bill } = salesOrder;
      const unchanged = () => {
        if (
          salesOrder.id_location === this.state.id_location &&
          salesOrder.id_customer === this.state.id_customer &&
          salesOrder.id_payment_category === this.state.paymentCategory.id_payment_category
        ) {
          return true;
        } else { return false; }
      }

      // if get discountBill
      if (discountBills && discountBills.length > 0) {
        // printing multiplier discount (first load updateForm)
        let alreadyGotBillDisc = discountBills.find(data => data.discount_code === this.state.activeCustDisc)
        if(this.state.activeCustDisc && alreadyGotBillDisc && !this.state.fullDiscMultiplier && alreadyGotBillDisc.discount_type === 4) {
          let multi = 1;

          alreadyGotBillDisc.discount_management_details.map(data => {
            multi *= ( 1 - (parseFloat(data.discount_value) / 100) );
          });

          const discountMulti = `${(1 - multi) * 100}%`
          setStateData.fullDiscMultiplier = salesOrder.discount;
          setStateData.custDisc = discountMulti;

          let string = salesOrder.discount;
          if (string.length > 8) {
            string = string.slice(0, 8) + "...";
          };
          setStateData.discount_unmasked = string;
          this.props.form.setFieldsValue({
            discount: convertDiscountMask(string)
          });

          // let string = ""
          // // print the discount
          // alreadyGotBillDisc.discount_management_details.map((data, index) => {
          //   if (index === alreadyGotBillDisc.discount_management_details.length - 1) {
          //     string += data.discount_value;
          //   } else {
          //     string += data.discount_value + "+";
          //   }
          // });

          // if (string.length > 8) {
          //   setStateData.fullDiscMultiplier = string
          //   string = string.slice(0, 8) + "...";
          // };
          // this.props.form.setFieldsValue({
          //   discount: convertDiscountMask(string)
          // });
        }

        setStateData.discBill = discountBills[0]

        this.setState(setStateData);
      }
      // diskon dari transaksi yang sudah di save sebelumnya
      else if (salesOrder.id_discount && discount_bill && unchanged()) {
        // render diskon type multiplier
        let alreadyGotBillDisc = Object.assign({}, discount_bill);
        if(this.state.activeCustDisc && alreadyGotBillDisc && !this.state.fullDiscMultiplier && alreadyGotBillDisc.discount_type === 4) {
          let multi = 1;

          alreadyGotBillDisc.discount_management_details.map(data => {
            multi *= ( 1 - (parseFloat(data.discount_value) / 100) );
          });

          const discountMulti = `${(1 - multi) * 100}%`
          setStateData.fullDiscMultiplier = salesOrder.discount;
          setStateData.custDisc = discountMulti;

          let string = salesOrder.discount;
          if (string.length > 8) {
            string = string.slice(0, 8) + "...";
          };
          setStateData.discount_unmasked = string;
          this.props.form.setFieldsValue({
            discount: convertDiscountMask(string)
          });
        }

        setStateData.discBill = discount_bill;

        this.setState(setStateData);
      }
      // if doesnt get any discountBill
      else {
        const { customer } = this.state;
        const disc = customer
          ? parseFloat(customer.discount) * 100 + "%"
          : 0;
        this.setState({
          activeCustDisc: null,
          custDisc: disc ? disc : "0%",
          discBill: null,
          fullDiscMultiplier: null,
          discount_unmasked: disc ? disc : "0%"
        });
        setFieldsValue({
          discount: disc ? convertDiscountMask(disc) : "0%"
        });
      }
    }

    // when loc/cust/pay changed
    if (this.props.salesOrder.update_sales_order_details !== prevProps.salesOrder.update_sales_order_details && this.props.salesOrder.updateItems) {
      const { setFieldsValue } = this.props.form;
      const { update_sales_order_details, SOTransactionDate } = this.props.salesOrder
      if (update_sales_order_details.length === 0) {
        this.setState({
          clear: true,
          itemDetails: { sales_order_details: [] }
        })
      } else {
        let setStateData = {}
        setStateData.calcDiscItem = true
        setStateData.items = update_sales_order_details
        this.setState(setStateData)
      }
      setFieldsValue({
        transaction_date: moment(SOTransactionDate, "YYYY-MM-DD HH:mm:ss")
      })
    }

    const { notAvailableItems } = this.state;
    // show warning item grouping setelah save
    if (
      !this.state.viewedInfoItemGrouping && notAvailableItems && 
      salesOrder && 
      (salesOrder.transaction_status === 4 || salesOrder.transaction_status === 6)
    ) {
      Modal.warning({
        title: "Warning",
        content: notAvailableItems,
        icon: <Icon type="info-circle" />,
        okText: "OK"
      });

      this.setState({ viewedInfoItemGrouping: true });
    }

    let trackChangeField = -1;
    const { discount_unmasked } = this.state;
    if (!this.state.dataChanged && discount_unmasked !== null) {
      const values = this.props.form.getFieldsValue();
      trackChangeField = findChanges(
        Object.assign(this.props.form.getFieldsValue(), {
          customer_po_date: values.customer_po_date
            ? this.props.form.getFieldValue("customer_po_date").format("YYYY-MM-DD HH:mm:ss")
            : null,
          ship_date: values.ship_date
            ? this.props.form.getFieldValue("ship_date").format("YYYY-MM-DD HH:mm:ss")
            : null,
          discount: parseFloat(discount_unmasked) 
        }),
        Object.assign({}, salesOrder, { discount: parseFloat(salesOrder.discount) }), [
          "sales_order_no", "transaction_date", "customer_credit_limit",
          "due_date", "description"
        ]
      );
    }
    
    // cek apakah ada perubahan data yang harus disave
    // sebelum dikonfirm
    if (trackChangeField > -1 && !this.state.dataChanged) {
      this.setState({ dataChanged: true })
    }
  }

  // calculating discount bill
  calcDiscBill = (total_amount, useStrict = false) => {
    const { discBill } = this.state;
    const { discountBills } = this.props.salesOrder;

    const checkDiscount = (discBill, discType, total_amount, discount_management_details) => {
      const { setFieldsValue } = this.props.form;
      // for discount_type 3
      if (discType === 3) {
        const findIndexDisc = discount_management_details.findIndex(
          data => parseFloat(total_amount) < parseFloat(data.min_qty_amount)
        );
        if (discount_management_details[findIndexDisc - 1]) {
          // passing the disc
          let discount_value = discount_management_details[findIndexDisc - 1].discount_value;
          this.setState({
            activeCustDisc: discBill
              ? discBill.discount_code
              : discountBills[0].discount_code,
            custDisc: discount_value,
            discount_unmasked: discount_value
          });
          const e = {
            target: {
              value: discount_value
            }
          };
          this.handleChangeCustDisc(e);
          setFieldsValue({
            discount: convertDiscountMask(discount_value)
          });
        } else {
          // if total_amount is passing the highest min_amount
          if (
            discount_management_details[discount_management_details.length - 1]
              .min_qty_amount <= total_amount
          ) {
            let discount_value = discount_management_details[
                discount_management_details.length - 1
              ].discount_value;
            this.setState({
              activeCustDisc: discBill.discount_code,
              custDisc: discount_value,
              discount_unmasked: discount_value
            });
            const e = {
              target: {
                value: discount_value
              }
            };
            this.handleChangeCustDisc(e);
            setFieldsValue({
              discount: convertDiscountMask(discount_value)
            });
          }
          // doesnt get any discount, doesn't pass any min_amount
          else {
            const { customer } = this.state;
            const disc = customer
              ? parseFloat(customer.discount) * 100 + "%"
              : 0;
            this.setState({
              activeCustDisc: null,
              custDisc: disc ? disc : "0%",
              fullDiscMultiplier: null,
              discount_unmasked: disc ? disc : "0%"
            });
            const e = {
              target: {
                value: disc ? disc : "0%"
              }
            };
            this.handleChangeCustDisc(e);
            setFieldsValue({
              discount: disc ? convertDiscountMask(disc) : "0%"
            });
          }
        }
      }
      // for discount_type 4
      else if (discType === 4) {
        var string = "";
        if (total_amount > 0) {
          // print the discount
          discount_management_details.map((data, index) => {
            if (index === discount_management_details.length - 1) {
              string += data.discount_value;
            } else {
              string += data.discount_value + "+";
            }
          });
          
          this.setState({
            fullDiscMultiplier: string,
            discount_unmasked: string
          });
          if (string.length > 8) {
            string = string.slice(0, 8) + "...";
          }
          setFieldsValue({
            discount: convertDiscountMask(string)
          });

          // create var for custDisc multiplier
          var custDiscMul,
            multi = 1;

          // calc the multiplier disc using map
          discount_management_details.map((data, index) => {
            // ------------------------
            multi *= ( 1 - (parseFloat(data.discount_value) / 100) );
            // ------------------------
          });
          custDiscMul = `${(1 - multi) * 100}%`;
          
          const e = {
            target: { value: custDiscMul }
          };

          this.setState({
            activeCustDisc: discBill.discount_code,
            custDisc: custDiscMul
          });

          this.handleChangeCustDisc(e);
        } else {
          const { customer } = this.state;
          const disc = customer
            ? parseFloat(customer.discount) * 100 + "%"
            : 0;
          string = disc ? disc : "0%";

          this.setState({
            activeCustDisc: null,
            custDisc: disc ? disc : "0%",
            fullDiscMultiplier: null,
            discount_unmasked: disc ? disc : "0%"
          });

          setFieldsValue({
            discount: convertDiscountMask(string)
          });

          const e = {
            target: { value: string }
          };
          this.handleChangeCustDisc(e);
        }
        
      }
      // tidak ada diskon
      else {
        this.setState({
          activeCustDisc: null,
          custDisc: "0%"
        });
        const e = {
          target: {
            value: "0%"
          }
        };
        this.handleChangeCustDisc(e);
        setFieldsValue({
          discount: "0%"
        });
      }
    }

    if (useStrict) {
      const { discount_management_details } = discBill ? discBill : [];
      const discType = discBill ? discBill.discount_type : null;
      checkDiscount(discBill, discType, total_amount, discount_management_details)
    } else {
      const { discount_management_details } = discBill
        ? discBill
        : discountBills[0];
      const discType = discBill
        ? discBill.discount_type
        : discountBills[0].discount_type;
        checkDiscount(discBill ? discBill : discountBills[0], discType, total_amount, discount_management_details);
    }
  };

  clearItem = (items, total_qty, sum_subtotal_discount_item) => {
    const { cust_disc, discBill, inclusive_tax } = this.state;
    
    if (discBill) {
      this.setState({
        item: null,
        itemDetails: { sales_order_details: items },
        total_qty: total_qty,
        sum_subtotal_discount_item,
        dataChanged: true
      }, () => this.calcDiscBill(sum_subtotal_discount_item))
    } else {
      const total_amount = getTotalAmount(items, cust_disc, inclusive_tax)
      this.setState({
        discount_amount: getSupplierDiscount(items, cust_disc),
        itemDetails: { sales_order_details: items },
        item: null,
        total_amount,
        sum_subtotal_discount_item,
        dataChanged: true,
        total_discount: getTotalDiscount(items, cust_disc, inclusive_tax),
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, cust_disc) : 0,
        total_qty: total_qty,
        total_tax:
          items.length > 0 ? getTotalTax(items, cust_disc, inclusive_tax) : 0,
      });
    }
  };

  // clearItems: if items are came from prev transaction
  clearItems = (items, total_qty, sum_subtotal_discount_item) => {
    // console.log("masuk ke clearItems", items)
    const { calcDiscItem, custDisc, cust_disc, discBill, inclusive_tax } = this.state;
    
    if (discBill) {
      this.setState({
        clear: false,
        items: [],
        itemDetails: { sales_order_details: items },
        total_qty: total_qty,
        sum_subtotal_discount_item
      }, () => this.calcDiscBill(sum_subtotal_discount_item))
    } else {
      let total_amount = getTotalAmount(items, cust_disc, inclusive_tax);
      if(calcDiscItem) {
        this.setState({
          clear: false,
          discount_amount: getSupplierDiscount(items, cust_disc),
          itemDetails: { sales_order_details: items },
          items: [],
          total_amount,
          sum_subtotal_discount_item,
          total_discount: getTotalDiscount(items, cust_disc, inclusive_tax),
          total_exclude_discount_tax:
            items.length > 0 ? getExlDiscTax(items, inclusive_tax, cust_disc) : 0,
          total_qty: total_qty,
          total_tax:
            items.length > 0 ? getTotalTax(items, cust_disc, inclusive_tax) : 0,
        }, this.doneCalcDiscItem());
      } else {
        total_amount = getTotalAmount(items, custDisc, inclusive_tax);
        this.setState({
          clear: false,
          discount_amount: getSupplierDiscount(items, custDisc),
          itemDetails: { sales_order_details: items },
          items: [],
          total_amount,
          total_discount: getTotalDiscount(items, custDisc, inclusive_tax),
          total_exclude_discount_tax:
            items.length > 0 ? getExlDiscTax(items, inclusive_tax, custDisc) : 0,
          total_qty: total_qty,
          total_tax:
            items.length > 0 ? getTotalTax(items, custDisc, inclusive_tax) : 0,
        });
      }
    }
  };

  /* doneCalcDiscItem:  change calcDiscItem into false when the calculation of discount items are done (for loc/cust/payment changed case)
  */
  doneCalcDiscItem = () => {
    // const { autoSaveTrans, itemDetails } = this.state
    this.setState({
      calcDiscItem: false
    })
  }

  getUpdateSalesOrderDetails = (idCustomer, idLocation, changed = false, idPaymentCategory, updateItems) => {
    if(typeof(updateItems)!== "boolean" && updateItems !== false) {
      updateItems = true
    }
    const { itemDetails, discBill } = this.state
    const { getFieldValue } = this.props.form
    let transaction_date

    if (changed) {
      transaction_date = moment().format("YYYY-MM-DD HH:mm:ss");
    } else {
      transaction_date = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss");
    }

    let values = {
      id_customer: idCustomer,
      id_location: idLocation,
      id_payment_category: idPaymentCategory,
      transaction_date,
      discount_bill: discBill,
      updateItems
    }

    const data = Object.assign(
      {},
      values,
      itemDetails
    );
    this.props.getUpdateSODetails(data);
  }

  handleChangePaymentCategory = value => {
    const { getItemsForSO } = this.props
    const { setFieldsValue } = this.props.form
    const { paymentCategories } = this.props.salesOrder
    const { id_customer, id_location, transDate } = this.state
    setFieldsValue({ id_payment_term: undefined, due_date: undefined });

    const paymentCategory = paymentCategories.find(data => data.id_payment_category === value)
    this.setState({
      paymentCategory
    })

    if (id_customer && id_location) {
      const { sales_order_details } = this.state.itemDetails
      if (sales_order_details.length > 0) {
        this.getUpdateSalesOrderDetails(id_customer, id_location, false, value)
      } else {
        getItemsForSO(id_customer, id_location, transDate, value);
      }
    }

  }

  handleChangeTransDate = status => {
    // console.log(status);
    if (!status) {
      const { setFieldsValue, getFieldValue } = this.props.form;
      const { getItemsForSO } = this.props;
      const { id_location, id_customer, paymentCategory } = this.state;
      const { paymentTerms } = this.props.salesOrder;
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === getFieldValue("id_payment_term")
      );
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
      const due_date = paymentTerm
        ? moment(transDate).add(paymentTerm.net_payment_day, "d")
        : null;
      // console.log("ganti tanggal", getFieldValue("id_payment_term"));
      this.setState({due_date})
      setFieldsValue({ due_date: due_date });
      if (id_location && id_customer) {
        getItemsForSO(id_customer, id_location, transDate, paymentCategory.id_payment_category);
        if (this.state.itemDetails.sales_order_details.length > 0) {
          // this.getDiscountItemList();
        }
      }
      this.setState({ transDate });
    }
  };

  handleChangeCustomer = id_customer => {
    const { id_location } = this.state;
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { getCustomerContacts, getItemsForSO } = this.props;
    const { currencies, customers, paymentCategories, paymentTerms } = this.props.salesOrder;
    const customer = customers.find(
      data => data.id_customer === id_customer
    );
    const currency = currencies.find(
      data => data.id_currency === customer.id_currency
    );
    const paymentCategory = paymentCategories.find(
      data => data.id_payment_category === customer.id_payment_category
    );
    const paymentTerm = paymentTerms.find(
      data => data.id_payment_term === customer.id_payment_term
    );
    // store data customer contact ke state
    if (customer.customer_contacts.length > 0) {
      const customerFindContact = [];
      for (var i = 0; i < customer.customer_contacts.length; i++) {
        customerFindContact.push(
          customer.customer_contacts[i].customer_contact_name
        );
      }
      this.setState({
        customerContactsName: customerFindContact
        // customerContacts: customer.customer_contacts
      });
    } else {
      this.setState({ customerContactsName: [] });
    }
    const currRate = currency
      ? currency.is_base
        ? 1
        : currency.currency_rate.rate
      : 1;
    const fiscRate = currency
      ? currency.is_base
        ? 1
        : currency.currency_rate.rate
      : 1;
    const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
    const due_date = paymentTerm
      ? moment(transDate).add(paymentTerm.net_payment_day, "d")
      : null;
    const disc = customer.discount
      ? parseFloat(customer.discount) * 100 + "%"
      : 0;

    if (id_location) {
      const { sales_order_details } = this.state.itemDetails
      if (sales_order_details.length > 0) {
        this.getUpdateSalesOrderDetails(id_customer,
          id_location,
          false,
          customer.id_payment_category)
      } else {
        getItemsForSO(id_customer, id_location, transDate, customer.id_payment_category);
      }
    }
    getCustomerContacts(id_customer);

    this.setState({
      clear: false,
      due_date,
      id_customer: id_customer,
      netPayDay: paymentTerm ? paymentTerm.net_payment_day : 0,
      netPayDisc: paymentTerm ? paymentTerm.discount : 0,
      paymentCategory,
      customer,
      discount_unmasked: disc
    });
    const e = {
      target: { value: disc }
    };
    this.handleChangeCustDisc(e);
    setFieldsValue({
      contact_person: null,
      customer_credit_limit: parseFloat(customer.credit_limit).toFixed(2).replace(
        /\B(?=(\d{3})+(?!\d))/g,
        ","
      ),
      id_currency: currency ? currency.id_currency : null,
      currency_rate: parseFloat(currRate).toFixed(2),
      fiscal_rate: parseFloat(fiscRate).toFixed(2),
      address_name: [],
      id_payment_category: customer.id_payment_category,
      id_payment_term: customer.id_payment_term,
      due_date: due_date,
      discount: convertDiscountMask(disc),
      phone: null,
      ship_to: null
    });
  };

  handleChangePaymentTerm = id_payment_term => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { paymentTerms } = this.props.salesOrder;
    const paymentTerm = paymentTerms.find(
      data => data.id_payment_term === id_payment_term
    );
    const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
    const due_date = paymentTerm
      ? moment(transDate).add(paymentTerm.net_payment_day, "d")
      : null;
    this.setState({
      due_date,
      netPayDay: paymentTerm.net_payment_day,
      netPayDisc: paymentTerm.discount
    });
    setFieldsValue({ due_date: due_date });
  };

  handleCustomerContact = value => {
    const { customerContactsName, id_customer } = this.state;
    const { customers } = this.props.salesOrder;
    const { setFieldsValue } = this.props.form;
    if (customerContactsName.length > 0) {
      const findCust = customers.find(
        data => data.id_customer === id_customer
      );
      const findCustCont = findCust.customer_contacts.find(
        data => data.customer_contact_name === value
      );
      if (findCustCont) {
        setFieldsValue({
          phone: findCustCont.phone
        });
      } else {
        setFieldsValue({
          phone: null
        });
      }
    } else {
      setFieldsValue({
        phone: null
      });
    }
  };

  handleModalCancelSO = () => {
    this.setState({
      visibleModalCancelSO: !this.state.visibleModalCancelSO
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      let { itemDetails, soDetails} = this.state
      const {
        custDisc,
        fullDiscMultiplier,
        discount_amount,
        total_tax,
        total_exclude_discount_tax,
        total_discount,
        total_amount,
        netPayDay,
        netPayDisc
        // id_item_request
      } = this.state;
      const { employees } = this.props.salesOrder;

      // validasi item
      if (itemDetails.sales_order_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      // cek diskon item
      let counter = 0;
      var item;
      for(item of itemDetails.sales_order_details) {
        if (item.sub_total < 0 && parseFloat(item.discount) > item.sub_total) {
          counter++;
          break;
        }
      }

      if (counter > 0) {
        this.setState({ err_discount: true });
        return;
      } else  {
        this.setState({ err_discount: false });
      }

      if (!soDetails.so_down_payment || !soDetails.so_down_payment.amount) {
        soDetails.so_down_payment = null;
      }

      // all date setup
      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      values.due_date = values.due_date
        ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.ship_date = values.ship_date
        ? values.ship_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.customer_po_date = values.customer_po_date
        ? values.customer_po_date.format("YYYY-MM-DD")
        : null;

      // freight cost setup
      values.total_freight_cost = this.state.total_freight_cost || 0;
      values.id_freight_account = this.state.id_freight_account;
      values.freight_include_in_total = this.state.freight_include_in_total;

      // values.customer_credit_limit =  values.customer_credit_limit ? parseFloat(
      //   values.customer_credit_limit.replace(/\$\s?|(,*)/g, "")
      // ) : null;


      // salesman set up
      if (values.id_salesman && values.id_salesman.length === 0) {
        values.id_salesman = null;
      } else if (values.id_salesman) {
        const findSalesman = employees.find(function(data) {
          return data.id_employee === values.id_salesman;
        });

        values.salesman_name =
          findSalesman.first_name +
          (findSalesman.last_name ? " " + findSalesman.last_name : "");
      }

      const so_deliv = {
        so_delivery: {
          ship_date: values.ship_date ? values.ship_date : null,
          ship_to: values.ship_to ? values.ship_to : null
        }
      };

      // handle bill discount
      values.id_discount = this.state.activeCustDisc
        ? this.state.discBill.id_discount_management
        : null;
      values.discount = values.id_discount && fullDiscMultiplier ? fullDiscMultiplier : custDisc;

      // set up itemDetails
      itemDetails.sales_order_details.map(data => data.delivered_qty = 0)

      const data = Object.assign({}, values, itemDetails, soDetails, so_deliv, {
        discount_amount: discount_amount,
        total_tax: total_tax,
        total_exclude_discount_tax: total_exclude_discount_tax,
        total_discount: total_discount,
        total_amount: total_amount,
        net_payment_day: netPayDay,
        net_payment_discount: netPayDisc,
        inclusive_tax: values.inclusive_tax ? values.inclusive_tax : false
        // id_item_request: id_item_request
      });

      if (!err) {
        // console.log("submitted data from update so", data);
        this.props.submit(data)
      } else {
        console.log(err)
      }
    });
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  // handleModalDownPayment = () => {
  //   const { visibleDownPayment } = this.state;
  //   visibleDownPayment
  //     ? this.setState({ visibleDownPayment: false })
  //     : this.setState({ visibleDownPayment: true });
  // };

  handleModalFreightCost = () => {
    const { visibleFreightCost } = this.state;
    visibleFreightCost
      ? this.setState({ visibleFreightCost: false })
      : this.setState({ visibleFreightCost: true });
  };

  handleModalPriceCompare = () => {
    const { visibleModalPriceCompare } = this.state;
    visibleModalPriceCompare
      ? this.setState({ visibleModalPriceCompare: false })
      : this.setState({ visibleModalPriceCompare: true });
  };

  handleModalUnconfirmSO = () => {
    this.setState({
      visibleModalUnconfirmSO: !this.state.visibleModalUnconfirmSO
    });
  };

  handleSelectItem = value => {
    const { infoMessages, items } = this.props.salesOrder;
    const { getItemsForSO } = this.props;
    const {
      search,
      id_customer,
      id_location,
      paymentCategory,
      transDate
    } = this.state;
    const item = items.find(item => item.id_item === value);
    item.id_unit = item.preference_item.id_sales_unit;
    const price_list = getSalesPriceAndDiscountNonDM(item)
    // if ((item.item_type !== 4 && item.item_type !== 3) && parseFloat(item.current_qty) <= 0) {
    //   notification("error", infoMessages.not_available_item);
    // } else 
    if (price_list.price <= 0) {
      notification("error", infoMessages.not_available_price);
    } else {
      item.price = price_list.price
      item.discount = price_list.discount
      this.setState({
        item
      });
    }

    if (search) {
      getItemsForSO(
        id_customer,
        id_location,
        transDate,
        paymentCategory.id_payment_category
      );
      this.setState({ search: null });
    }
  };

  handleSelectLocation = id_location => {
    const { getItemsForSO } = this.props;
    const { getFieldValue } = this.props.form;
    const { id_customer, paymentCategory } = this.state;

    this.setState({
      id_location: id_location,
      // clear: this.state.id_location !== id_location ? true : false
    });
    if (id_customer) {
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
      const { sales_order_details } = this.state.itemDetails
      if (sales_order_details.length > 0) {
        this.getUpdateSalesOrderDetails(
          id_customer,
          id_location,
          false,
          paymentCategory.id_payment_category,
          true
        )
      } else {
        getItemsForSO(id_customer, id_location, transDate, paymentCategory.id_payment_category);
      }
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.salesOrder;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_sales_order === value
    );
    this.setState({
      items: transaction.sales_order_details,
      itemDetails: {
        sales_order_details: transaction.sales_order_details
      },
      id_location: transaction.id_location,
      id_supplier: transaction.id_supplier,
      inclusive_tax: transaction.inclusive_tax ? true : false,
      netPayDay: transaction.net_payment_day,
      netPayDisc: transaction.net_payment_discount
    });
    setFieldsValue({
      sales_order_no: transaction.sales_order_no,
      id_location: transaction.id_location,
      id_customer: transaction.id_customer
    });
    this.handleChangeCustomer(transaction.id_customer);
    this.handleSelectLocation(transaction.id_location);
    this.handleModal();
  };

  handleInclusiveTax = e => {
    const { inclusive_tax } = this.state;
    this.setState({ inclusive_tax: !inclusive_tax });
  };

  handleSearchItem = value => {
    const { getItemsForSO } = this.props;
    const { id_customer, id_location, paymentCategory } = this.state;
    const { getFieldValue } = this.props.form;
    // console.log(this.props.salesOrder);
    this.setState({ search: value });
    if (id_customer && id_location) {
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss")
      getItemsForSO(id_customer, id_location, transDate, paymentCategory.id_payment_category, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleChangeCustDisc = e => {
    // console.log("handleChangeCustDisc called")
    const { itemDetails, inclusive_tax } = this.state;
    const { sales_order_details } = itemDetails;
    const value = e.target.value ? e.target.value : "0";

    this.setState({
      custDisc: value,
      cust_disc: value,
      discount_amount: getSupplierDiscount(sales_order_details, value),
      total_tax: getTotalTax(sales_order_details, value, inclusive_tax),
      total_exclude_discount_tax: getExlDiscTax(
        sales_order_details,
        inclusive_tax,
        value
      ),
      total_discount: getTotalDiscount(sales_order_details, value, inclusive_tax),
      total_amount: getTotalAmount(sales_order_details, value, inclusive_tax) });
  };

  handleSaveItemList = (items, total_qty, sum_subtotal_discount_item, calc_discount = true, dataChanged = false) => {
    const { cust_disc, discBill, inclusive_tax } = this.state;
    
    if(discBill && calc_discount) {
      this.setState({
        clear: false,
        itemDetails: { sales_order_details: items },
        total_qty: total_qty,
        sum_subtotal_discount_item,
        dataChanged
      }, () => this.calcDiscBill(sum_subtotal_discount_item))
    } else {
      const total_amount = getTotalAmount(items, cust_disc, inclusive_tax)
      this.setState({
        clear: false,
        discount_amount: getSupplierDiscount(items, cust_disc),
        itemDetails: { sales_order_details: items },
        total_amount,
        sum_subtotal_discount_item,
        dataChanged,
        total_discount: getTotalDiscount(items, cust_disc, inclusive_tax),
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, cust_disc) : 0,
        total_qty: total_qty,
        total_tax:
          items.length > 0 ? getTotalTax(items, cust_disc, inclusive_tax) : 0,
      });
    }
  };

  // handleOkDownPayment = (value, remainClosed) => {
  //   const { setFieldsValue } = this.props.form;
  //   setFieldsValue({ amount: value.amount });
  //   value.amount = parseFloat(value.amount.replace(/\$\s?|(,*)/g, ""));

  //   let so_down_payment = null
  //   if (!value.id_cash_bank) {
  //     so_down_payment = null
  //   } else {
  //     so_down_payment = {
  //       ...this.state.soDetails.so_down_payment,
  //       ...value
  //     }
  //   }

  //   this.setState({
  //     soDetails: {
  //       so_down_payment
  //     }
  //   });

  //   if(!remainClosed) {
  //     this.handleModalDownPayment();
  //   }
  // };

  handleOkFreightCost = value => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({ total_freight_cost: value.total_freight_cost });
    value.total_freight_cost = value.total_freight_cost
      ? parseFloat(value.total_freight_cost.replace(/\$\s?|(,*)/g, ""))
      : 0;

    this.setState({
      freight_include_in_total: value.freight_include_in_total,
      id_freight_account: value.id_freight_account,
      total_freight_cost: value.total_freight_cost
    });

    this.handleModalFreightCost();
  };

  // handleCancelDownPayment = () => {
  //   const { setFieldsValue } = this.props.form;
  //   setFieldsValue({ amount: "0.00" });
  //   this.setState({
  //     soDetails: {
  //       so_down_payment: {
  //         // id_purchase_order: null,
  //         // id_cash_bank: null,
  //         // id_dp_account: null,
  //         // id_cash_flow_type: null,
  //         // bank_issuer: null,
  //         // reference_no: null,
  //         // dp_due_date: null,
  //         // amount: null
  //       }
  //     },
  //     visibleDownPayment: false
  //   });
  // };

  handleCancelFreightCost = () => {
    const { transStatus } = this.state;
    if (transStatus === 4 || transStatus === 6) {
      const { setFieldsValue } = this.props.form;
      setFieldsValue({ total_freight_cost: "0.00" });
    }
    this.setState({
      visibleFreightCost: false
    });
  };

  renderAddress = id_address => {
    const { setFieldsValue } = this.props.form;
    const { customerAddresses } = this.props.salesOrder;
    const findAdd = customerAddresses.find(
      data => data.id_customer_address === id_address
    );
    var string =
      findAdd.customer_address_name +
      " - " +
      findAdd.address +
      (findAdd.district ? ", " + findAdd.district.district_name + ", " : "") +
      (findAdd.city
        ? findAdd.city.city_type + " " + findAdd.city.city_name + ", "
        : "") +
      (findAdd.province ? findAdd.province.province_name + ", " : "") +
      (findAdd.country ? findAdd.country.country_name : "");
    if (findAdd.zip_code) {
      string += " " + findAdd.zip_code;
    }
    setFieldsValue({ ship_to: string });
  };

  updateSalesPrice = lastUpdatedTransDate => {
    const { update_sales_order_details, SOTransactionDate, discountBills } = this.props.salesOrder
    const { sum_subtotal_discount_item } = this.state

    let setStateData = {}
    setStateData.calcDiscItem = true
    setStateData.items = update_sales_order_details
    setStateData.calculatingDiscount = true
    setStateData.discBill = discountBills[0] ? discountBills[0] : {}

    this.setState(setStateData, () => this.calcDiscBill(sum_subtotal_discount_item, true))
    this.props.form.setFieldsValue({
      transaction_date: moment(SOTransactionDate, "YYYY-MM-DD HH:mm:ss")
    })
    this.handleModalPriceCompare()
  }

  confirmInvoiceAfterCompare = () => {
    let { itemDetails, soDetails} = this.state
    const {
      custDisc,
      fullDiscMultiplier,
      discount_amount,
      total_tax,
      total_exclude_discount_tax,
      total_discount,
      total_amount,
      netPayDay,
      netPayDisc
      // id_item_request
    } = this.state;
    let values = this.props.form.getFieldsValue()
    const { employees } = this.props.salesOrder;

    if (!soDetails.so_down_payment || !soDetails.so_down_payment.amount) {
      soDetails.so_down_payment = null;
    }

    // all date setup
    values.transaction_date = values.transaction_date.format(
      "YYYY-MM-DD HH:mm:ss"
    );
    values.due_date = values.due_date
      ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    values.ship_date = values.ship_date
      ? values.ship_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    values.customer_po_date = values.customer_po_date
      ? values.customer_po_date.format("YYYY-MM-DD")
      : null;

    // freight cost setup
    values.total_freight_cost = this.state.total_freight_cost || 0;
    values.id_freight_account = this.state.id_freight_account;
    values.freight_include_in_total = this.state.freight_include_in_total;

    // values.customer_credit_limit = parseFloat(
    //   values.customer_credit_limit.replace(/\$\s?|(,*)/g, "")
    // );


    // salesman set up
    if (values.id_salesman && values.id_salesman.length === 0) {
      values.id_salesman = null;
    } else if (values.id_salesman) {
      const findSalesman = employees.find(function(data) {
        return data.id_employee === values.id_salesman;
      });

      values.salesman_name =
        findSalesman.first_name +
        (findSalesman.last_name ? " " + findSalesman.last_name : "");
    }

    const so_deliv = {
      so_delivery: {
        ship_date: values.ship_date ? values.ship_date : null,
        ship_to: values.ship_to ? values.ship_to : null
      }
    };
    
    // handle bill discount
    values.id_discount = this.state.activeCustDisc
      ? this.state.discBill.id_discount_management
      : null;
    values.discount = values.id_discount && fullDiscMultiplier ? fullDiscMultiplier : custDisc;
    
    // set up itemDetails
    itemDetails.sales_order_details.map(data => data.delivered_qty = 0)

    const data = Object.assign({}, values, itemDetails, soDetails, so_deliv, {
      discount_amount: discount_amount,
      total_tax: total_tax,
      total_exclude_discount_tax: total_exclude_discount_tax,
      total_discount: total_discount,
      total_amount: total_amount,
      net_payment_day: netPayDay,
      net_payment_discount: netPayDisc,
      inclusive_tax: values.inclusive_tax ? values.inclusive_tax : false
      // id_item_request: id_item_request
    });
    
    this.props.confirmSalesOrder(
      this.props.salesOrder.salesOrder.id_sales_order,
      data
    );
  }

  render() {
    // console.log(this.props.salesOrder);
    const { search, visible } = this.state;
    const {
      getFieldDecorator,
      getFieldValue,
      // setFieldsValue
    } = this.props.form;
    const { salesOrder } = this.props;
    const {
      // accounts,
      // cashBanks,
      // cashFlowTypes,
      customers,
      customerAddresses,
      departments,
      discountItems,
      employees,
      items,
      locations,
      paymentCategories,
      paymentTerms,
      projects,
      couriers,
      taxes
    } = salesOrder ? salesOrder : [];
    // console.log(employees);
    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingCancel,
      loadingClose,
      loadingConfirm,
      loadingItems,
      // loadingItemRequests,
      loadingForeign,
      loadingSubmit,
      loadingTransactions,
      loadingUnconfirm,
      loadingUpdateSalesOrderDetails
      // id_location
    } = salesOrder ? salesOrder : [];
    const data = salesOrder.salesOrder ? salesOrder.salesOrder : [];

    // const used = data.is_used;
    const error = salesOrder
      ? salesOrder.errorField
        ? salesOrder.errorField
        : []
      : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const transactionStatus = () => {
      let curr = 0;
      switch (data.transaction_status) {
        case 4:
          curr = 0;
          break;
        case 6:
          curr = 0;
          break;
        case 1:
          curr = 1;
          break;
        case 2:
          curr = 2;
          break;
        case 3:
          curr = 3;
          break;
        case 5:
          curr = 4;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    const deliveryStatus = () => {
      let className = null;
      let display = null;

      switch (data.delivery_status) {
        case 0:
          className = "new-receipt inside-form";
          display = getLanguage({ id: "new" });
          break;
        case 1:
          className = "partial-receipt inside-form";
          display = getLanguage({ id: "partialDelivery" });
          break;
        case 2:
          className = "full-receipt inside-form";
          display = getLanguage({ id: "fullDelivery" });
          break;
        default:
          className = "new-receipt inside-form";
          display = getLanguage({ id: "new" });
          break;
      }
      return <StatusTag className={className}>{display}</StatusTag>;
      // return { className: className, display: display };
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          salesOrder={salesOrder}
          id_location={this.state.id_location}
          id_customer={this.state.id_customer}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <ModalCancelSO
          handleModal={this.handleModalCancelSO}
          visible={this.state.visibleModalCancelSO}
          submit={this.props.handleSubmitCancelSO}
          loading={loadingCancel}
        />

        {/* <ModalFreightCost
          accounts={accounts}
          dataSalesOrder={data}
          handleModal={this.handleModalFreightCost}
          // handleCancelModal={this.handleCancelFreightCost}
          salesOrder={salesOrder}
          submit={this.handleOkFreightCost}
          transactionStatus={data.transaction_status}
          visible={this.state.visibleFreightCost}
        /> */}

        <ModalPriceCompare
          data={this.state.comparePriceData}
          handleModal={this.handleModalPriceCompare}
          submitPrev={() => {
            this.confirmInvoiceAfterCompare()
            this.setState({ visibleModalPriceCompare: false });
          }}
          updateSalesPrice={lastUpdatedTransDate => this.updateSalesPrice(lastUpdatedTransDate)}
          visible={this.state.visibleModalPriceCompare}
        />

        {/* <ModalDownPayment
          accounts={accounts}
          cashBanks={cashBanks}
          cashFlowTypes={cashFlowTypes}
          dueDate={this.state.due_date}
          handleModal={this.handleModalDownPayment}
          id_location={this.state.id_location}
          loadingForeign={loadingForeign}
          salesOrder={{ salesOrder: this.state.soDetails }}
          submit={this.handleOkDownPayment}
          transaction_status={data.transaction_status}
          totalAmount={this.state.total_amount}
          transDate={this.state.transDate}
          visible={visibleDownPayment}
        /> */}

        <ModalUnconfirmSO
          handleModal={this.handleModalUnconfirmSO}
          submit={this.props.handleSubmitUnconfirmSO}
          visible={this.state.visibleModalUnconfirmSO}
          loading={loadingUnconfirm}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps
              size="small"
              progressDot={customDot}
              current={transactionStatus()}
            >
              <Step title={getLanguage({ id: "new" })} />
              <Step title={getLanguage({ id: "ordered" })} />
              <Step title={getLanguage({ id: "delivered" })} />
              <Step title={getLanguage({ id: "closed" })} />
              <Step title={getLanguage({ id: "cancelled" })} />
            </Steps>
          </Col>
        </Row>

        <Tabs
          defaultActiveKey="1"
          onTabClick={key => this.setState({ tab: key })}
        >
          <TabPane tab={<IntlMessages id="salesOrders.tabs.detail" />} key={1}>
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.salesOrderNo" />}
                      >
                        {getFieldDecorator("sales_order_no", {
                          initialValue: data.sales_order_no || null
                        })(
                          <Input
                            disabled={true}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.transactionDate" />
                        }
                        style={{ marginBottom: 0, paddingBottom: 0 }}
                      >
                        {getFieldDecorator("transaction_date", {
                          initialValue: data.transaction_date
                            ? moment(
                                data.transaction_date,
                                "YYYY-MM-DD HH:mm:ss"
                              )
                            : null
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            // onChange={this.handleChangeTransDate}
                            onOpenChange={this.handleChangeTransDate}
                            disabled={true}
                            allowClear={false}
                            disabledDate={e => e.valueOf() > moment()}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12} style={{ paddingRight: "5px" }}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.location" />}
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: data.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                            showSearch
                            style={{ width: "98.5%" }}
                            placeholder={
                              <IntlMessages id="salesOrders.location" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleSelectLocation}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations.map((data, key) => (
                              <Option key={key} value={data.id_location}>
                                {`${data.location_code} (${data.location_name})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.customer" />}
                      >
                        {getFieldDecorator("id_customer", {
                          initialValue: data.id_customer,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="salesOrders.customer" />
                            }
                            loading={loadingForeign}
                            onSelect={(value) => this.handleChangeCustomer(value)}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {customers.map((data, key) => (
                              <Option key={key} value={data.id_customer}>
                                {`${data.customer_code} (${
                                  data.customer_name
                                })`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>

                <Col span={24}>
                  <InputGroup>
                    {/* form item untuk customer po no */}
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.customerPOInfo.no" />
                        }
                      >
                        {getFieldDecorator("customer_po_no", {
                          initialValue: data.customer_po_no
                        })(
                          <Input
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                            placeholder={getLanguage({ id: "typeCustomerPONo" })}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.customerPOInfo.date" />
                        }
                      >
                        {getFieldDecorator("customer_po_date", {
                          initialValue: data.customer_po_date
                            ? moment(data.customer_po_date, "YYYY-MM-DD")
                            : null
                        })(
                          <DatePicker
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                            disabledDate={e =>
                              e.valueOf() >
                              moment(this.state.transDate, "YYYY-MM-DD").endOf(
                                "day"
                              )
                            }
                            style={{ width: "100%" }}
                            format="DD MMMM YYYY"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row>
                  <Form.Item
                    {...formTextArea}
                    style={{ paddingBottom: "12px" }}
                    label={<IntlMessages id="salesOrders.note" />}
                  >
                    {getFieldDecorator("note", {
                      initialValue: data.note
                    })(
                      <TextArea
                        disabled={
                          data.transaction_status === 4 ||
                          data.transaction_status === 6
                            ? false
                            : true
                        }
                        autosize={{ minRows: 4 }}
                      />
                    )}
                  </Form.Item>
                </Row>
                <Row>
                  <Col span={24}>
                    {data.transaction_status === 4 || data.transaction_status === 6 ? (
                      <Col span={10} style={{ paddingRight: "10px" }}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="salesOrders.creditLimit" />}
                        >
                          {getFieldDecorator("customer_credit_limit", {
                            initialValue: parseFloat(data.customer_credit_limit)
                              .toFixed(2)
                              .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          })(
                            <Input disabled={true} />
                          )}
                        </Form.Item>
                      </Col>
                    ) : null}
                    <Col span={6}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.deliveryStatus" />}
                      >
                        {deliveryStatus()}
                      </Form.Item>
                    </Col>
                  </Col>
                </Row>
              </Col>
            </Row>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="itemDetails" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  // label={<IntlMessages id="salesOrders.item" />}
                  validateStatus={
                    error.sales_order_details || this.state.err_item 
                      ? "error" 
                      : null
                  }
                  help={
                    error.sales_order_details 
                      ? error.sales_order_details 
                      : this.state.err_item
                        ? <IntlMessages id="itemDataEmpty" />
                        : null
                  }
                  extra={<IntlMessages id="salesOrders.item.help" />}
                >
                  <Select
                    disabled={
                      data.transaction_status === 4 ||
                      data.transaction_status === 6
                        ? false
                        : true
                    }
                    value={search ? search : []}
                    showSearch
                    allowClear
                    // optionFilterProp="children"
                    filterOption={false}
                    placeholder={
                      <IntlMessages id="salesOrders.item.placeholder" />
                    }
                    loading={loadingItems}
                    notFoundContent={
                      loadingItems ? (
                        <Spin size="small" />
                      ) : this.state.id_location && this.state.id_customer ? (
                        <Empty description={<IntlMessages id="noData" />} />
                      ) : (
                        <Empty
                          description={
                            <IntlMessages id="salesOrders.item.noSelection" />
                          }
                        />
                      )
                    }
                    onSelect={this.handleSelectItem}
                    onChange={this.handleChangeItem}
                    onSearch={this.handleSearchItem}
                    optionFilterProp="children"
                    filterOption={true}
                    value={undefined}
                  >
                    {this.state.id_location && this.state.id_customer ? items.map(data => (
                      <Option key={data.id_item} value={data.id_item}>
                        {data.item_code + " - " + data.item_name}
                      </Option>
                    )) : null}
                  </Select>
                </Form.Item>
              </Col>
              <Col span={24}>
                <EditableTable
                  calcDiscItem={this.state.calcDiscItem}
                  clear={this.state.clear}
                  clearItem={this.clearItem}
                  clearItems={this.clearItems}
                  departments={departments}
                  discountItems={discountItems}
                  exceededItems={this.state.exceededItems}
                  handleSaveItemList={this.handleSaveItemList}
                  inclusive_tax={this.state.inclusive_tax}
                  item={this.state.item}
                  items={this.state.items}
                  itemsFromForeignKeys={items}
                  loadingItems={loadingItems}
                  loadingTable={
                    loadingItems &&
                    this.state.itemDetails.sales_order_details.length > 0
                  }
                  loadingTransactions={loadingTransactions}
                  loadingTable={loadingUpdateSalesOrderDetails}
                  projects={projects}
                  custDisc={this.state.custDisc}
                  taxes={taxes}
                  transaction_status={data.transaction_status}
                />
              </Col>
              <Col span={12}>
                <Col span={10} style={{ marginTop: "15px", textAlign: "left" }}>
                  <strong>
                    <IntlMessages id="purchaseInvoices.totalQty" />
                  </strong>
                  <strong>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </strong>
                </Col>
                <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                  <strong>
                    <NumberFormat value={this.state.total_qty} />
                  </strong>
                </Col>
              </Col>
            </Row>

            <Row>
              <Col span={14} offset={10} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.exlDiscountAndTax" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_exclude_discount_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.discount" />
                    {this.state.activeCustDisc ? (
                      <Tooltip
                        placement="bottom"
                      >
                        <StatusTag
                          className="billed-payment"
                          style={{ marginLeft: "4px" }}
                        >
                          {this.state.activeCustDisc}
                        </StatusTag>
                      </Tooltip>
                    ) : null}
                  </Col>
                  <Col span={4} offset={1} style={{ marginLeft: "5px" }}>
                    <Tooltip
                      placement="bottom"
                      title={this.state.fullDiscMultiplier || ""}
                    >
                      {getFieldDecorator("discount", {
                        initialValue: 0
                      })(
                        <Input
                          size="small"
                          disabled={
                            !permissions.includes("Edit Discount Sales Order") ||
                            (data.transaction_status !== 4 &&
                              data.transaction_status !== 6)
                          }
                          // readOnly
                          onChange={this.handleChangeCustDisc}
                          style={{ 
                            marginTop: "3px",
                            borderColor: this.state.total_exclude_discount_tax - this.state.discount_amount < 0
                              ? "#f5222d"
                              : "#d9d9d9"
                          }}
                        />
                      )}
                    </Tooltip> 
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.totalDiscount" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.totalTax" />
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="total" />
                    </strong>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_amount} />
                    </span>
                  </Col>
                </Row>

                {/* <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.freightCost" />
                  </Col>
                  <Col span={8} offset={1}>
                    {getFieldDecorator("total_freight_cost", {
                      initialValue: data.total_freight_cost
                        ? data.total_freight_cost.replace(
                            /\B(?=(\d{3})+(?!\d))/g,
                            ","
                          )
                        : 0
                    })(
                      <Input
                        disabled={
                          data.transaction_status === 4 ||
                          data.transaction_status === 6
                            ? false
                            : true
                        }
                        readOnly={true}
                        onClick={this.handleModalFreightCost}
                        addonAfter={
                          <Tooltip
                            placement="bottom"
                            title={
                              <IntlMessages id="purchaseInvoices.setFreightCost" />
                            }
                          >
                            <ButtonAntd
                              style={{
                                width: "100%",
                                height: "30px",
                                border: "0px",
                                backgroundColor: "transparent"
                              }}
                              loading={loadingForeign}
                              disabled={loadingForeign}
                              onClick={this.handleModalFreightCost}
                              icon="plus"
                            />
                          </Tooltip>
                        }
                      />
                    )}
                  </Col>
                </Row> */}

                {/* <Row type="flex" justify="end" style={{ alignItems: "center", marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="purchaseOrders.downPayment" />
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="salesOrders.downPayment.help" />
                        }
                      >
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    </strong>
                  </Col>
                  <Col span={8} offset={1}>
                    {getFieldDecorator("amount", {
                      initialValue: data.so_down_payment
                        ? parseFloat(data.so_down_payment.amount).toFixed(2).replace(
                            /\B(?=(\d{3})+(?!\d))/g,
                            ","
                          )
                        : 0
                    })(
                      <Input
                        disabled={
                          data.transaction_status === 4 ||
                            data.transaction_status === 6
                            ? false
                            : true
                        }
                        readOnly={true}
                        onClick={this.handleModalDownPayment}
                        addonAfter={
                          <Tooltip
                            placement="bottom"
                            title={
                              <IntlMessages id="purchaseOrders.setDownPayment" />
                            }
                          >
                            <ButtonAntd
                              style={{
                                width: "100%",
                                height: "30px",
                                border: "0px",
                                backgroundColor: "transparent"
                              }}
                              loading={loadingForeign}
                              disabled={loadingForeign}
                              onClick={this.handleModalDownPayment}
                              icon="plus"
                            />
                          </Tooltip>
                        }
                      />
                    )}
                  </Col>
                </Row> */}
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="salesOrders.tab.paymentAndShipment" />}
            key={2}
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.payment" />
                </Divider>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.paymentCategory" />
                      }
                    >
                      {getFieldDecorator("id_payment_category", {
                        initialValue: data.payment_category
                          ? data.payment_category.id_payment_category
                          : null,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentCategory" />
                          }
                          loading={loadingForeign}
                          onChange={this.handleChangePaymentCategory}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {paymentCategories.map((data, key) => (
                            <Option key={key} value={data.id_payment_category}>
                              {data.payment_category_code}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.paymentTerm" />}
                    >
                      {getFieldDecorator("id_payment_term", {
                        initialValue: data.id_payment_term
                          ? data.payment_term.id_payment_term
                          : null,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          filterOption={true}
                          loading={loadingForeign}
                          onSelect={this.handleChangePaymentTerm}
                          optionFilterProp="children"
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentTerm" />
                          }
                          showSearch
                          style={{ width: "100%" }}
                        >
                          {getFieldValue("id_payment_category")
                            ? paymentTerms.map((data, key) => {
                                const paymentCategory = paymentCategories.find(
                                  data =>
                                    data.id_payment_category ===
                                    getFieldValue("id_payment_category")
                                );
                                if (
                                  paymentCategory.payment_type === 2 &&
                                  data.is_cash === 0
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                } else if (
                                  paymentCategory.payment_type !== 2 &&
                                  data.is_cash === 1
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                }
                              })
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <InputGroup>
                  <Col span={12} style={{ marginBottom: "-6px" }}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.dueDate" />}
                    >
                      {getFieldDecorator("due_date", {
                        initialValue: moment(data.due_date, "YYYY-MM-DD"),
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <DatePicker
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesman" />}
                    >
                      {getFieldDecorator("id_salesman", {
                        initialValue: data.id_salesman ? data.id_salesman : null
                      })(
                        <Select
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          showSearch
                          style={{ width: "100%" }}
                          filterOption={true}
                          optionFilterProp="children"
                          placeholder={<IntlMessages id="salesman.select" />}
                          loading={loadingForeign}
                        >
                          {employees
                            ? employees.map((data, key) => (
                                <Option key={key} value={data.id_employee}>
                                  {data.employee_code} ({""}
                                  {data.last_name
                                    ? data.first_name + " " + data.last_name
                                    : data.first_name}
                                  {""})
                                </Option>
                              ))
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <Row>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.inclusiveTax" />}
                    >
                      {getFieldDecorator("inclusive_tax", {
                        initialValue: data.inclusive_tax
                      })(
                        <Radio.Group
                          onChange={this.handleInclusiveTax}
                          // value={this.state.inclusive_tax}
                          buttonStyle="solid"
                        >
                          <Radio.Button
                            value={0}
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                          >
                            <IntlMessages id="no" />
                          </Radio.Button>
                          <Radio.Button
                            value={1}
                            disabled={
                              data.transaction_status === 4 ||
                              data.transaction_status === 6
                                ? false
                                : true
                            }
                          >
                            <IntlMessages id="yes" />
                          </Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.shipment" />
                </Divider>
                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="itemTransfers.courier" />}
                    >
                      {getFieldDecorator("id_courier", {
                        initialValue: data.id_courier
                      })(
                        <Select
                          showSearch
                          allowClear
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="itemTransfers.courier" />
                          }
                          loading={loadingForeign}
                          optionFilterProp="children"
                          filterOption={true}
                          disabled={
                            data.transaction_status !== 4 &&
                            data.transaction_status !== 6
                          }
                        >
                          {couriers.map((data, key) => (
                            <Option key={key} value={data.id_courier}>
                              {`${data.courier_name} (${data.description})`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="customers.tab.contact" />}
                    >
                      {getFieldDecorator("contact_person", {
                        initialValue: data.contact_person
                      })(
                        <AutoComplete
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          style={{ width: "100%" }}
                          dataSource={this.state.customerContactsName}
                          placeholder={<IntlMessages id="contactPerson" />}
                          filterOption={(inputValue, option) =>
                            option.props.children
                              .toUpperCase()
                              .indexOf(inputValue.toUpperCase()) !== -1
                          }
                          onBlur={this.handleCustomerContact}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="phone" />}
                    >
                      {getFieldDecorator("phone", { initialValue: data.phone })(
                        <Input
                          placeholder={getLanguage({ id: "phone" })}
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          style={{ width: "100%" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>

                <Row>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <InputGroup>
                      <Col span={24}>
                        <Form.Item
                          {...formItemLayout}
                          label={
                            <Tooltip
                              placement="top"
                              title={<IntlMessages id="address.select.help" />}
                            >
                              <IntlMessages id="address.select" />
                              <Icon
                                style={{ marginLeft: "5px" }}
                                type="question-circle"
                                theme="filled"
                              />
                            </Tooltip>
                          }
                        >
                          {getFieldDecorator("address_name", {
                            // initialValue: data.id_payment_term
                          })(
                            <Select
                              disabled={
                                data.transaction_status === 4 ||
                                data.transaction_status === 6
                                  ? false
                                  : true
                              }
                              showSearch
                              filterOption={true}
                              optionFilterProp="children"
                              style={{ width: "100%" }}
                              onSelect={this.renderAddress}
                              placeholder={<IntlMessages id="address.select" />}
                              loading={loadingForeign}
                              notFoundContent={
                                this.state.id_customer ? (
                                  <Empty
                                    description={
                                      <IntlMessages id="customers.noAddress" />
                                    }
                                  />
                                ) : (
                                  <Empty
                                    description={
                                      <IntlMessages id="pleaseSelectCustomerFirst" />
                                    }
                                  />
                                )
                              }
                            >
                              {customerAddresses && customerAddresses.length > 0
                                ? customerAddresses.map((data, key) => (
                                    <Option
                                      key={key}
                                      value={data.id_customer_address}
                                    >
                                      {data.address} ({""}
                                      {data.customer_address_name}
                                      {""})
                                    </Option>
                                  ))
                                : null}
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </InputGroup>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      style={{ paddingBottom: 0 }}
                      label={<IntlMessages id="salesOrders.shipDate" />}
                    >
                      {getFieldDecorator("ship_date", {
                        initialValue: data.ship_date
                          ? moment(data.ship_date, "YYYY-MM-DD HH:mm:ss")
                          : null
                      })(
                        <DatePicker
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          allowClear={true}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="salesOrders.shipTo" />}
                    >
                      {getFieldDecorator("ship_to", {
                        initialValue: data.ship_to
                      })(
                        <TextArea
                          disabled={
                            data.transaction_status === 4 ||
                            data.transaction_status === 6
                              ? false
                              : true
                          }
                          autosize={{ minRows: 4 }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            key={3}
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
          >
            <SalesOrderDocUpdateForm
              submit={this.handleSubmit}
              salesOrder={this.props.salesOrder}
              params={this.props.params}
              form={this.props.form}
            />
          </TabPane>
        </Tabs>

        {this.state.tab !== "3" ? (
          <Form.Item {...tailFormItemLayout}>
            <Button
              disabled={
                (data.transaction_status !== 4 &&
                  data.transaction_status !== 6) ||
                !permissions.includes("Update Sales Order") ||
                this.state.total_exclude_discount_tax - this.state.total_discount < 0
                  ? true
                  : false
              }
              htmlType="submit"
              loading={loading || loadingSubmit}
              onClick={this.handleSubmit}
              style={{ marginTop: "24px", marginRight: "5px" }}
              type="primary"
            >
              <IntlMessages id="button.submit" />
            </Button>

            {data.transaction_status === 4 || data.transaction_status === 6 ? (
              // Confirm
              <Popconfirm
                title={<IntlMessages id="confirmTransaction" />}
                onConfirm={() => this.getUpdateSalesOrderDetails(
                  this.state.id_customer,
                  this.state.id_location,
                  true,
                  this.state.paymentCategory.id_payment_category,
                  false
                )}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Button
                  style={{ marginRight: "5px", padding: "0px 15px" }}
                  disabled={
                    (data.transaction_status !== 4 &&
                      data.transaction_status !== 6) ||
                    this.state.dataChanged ||
                    !permissions.includes("Confirm Sales Order")
                      ? true
                      : false
                  }
                  loading={loadingConfirm || loadingUpdateSalesOrderDetails}
                >
                  <IntlMessages id="button.confirmTransaction" />
                </Button>
              </Popconfirm>
              // <Popconfirm
              //   title={<IntlMessages id="confirmTransaction" />}
              //   onConfirm={() =>
              //     this.props.confirmSalesOrder(data.id_sales_order)
              //   }
              // okText={<IntlMessages id="yes" />}
              // cancelText={<IntlMessages id="no" />}
              // >
              //   <Button
              //     style={{ marginRight: "5px", padding: "0px 15px" }}
              //     disabled={
              //       (data.transaction_status !== 4 &&
              //         data.transaction_status !== 6) ||
              //       !permissions.includes("Confirm Sales Order")
              //         ? true
              //         : false
              //     }
              //     loading={loadingConfirm}
              //   >
              //     <IntlMessages id="button.confirmTransaction" />
              //   </Button>
              // </Popconfirm>
            ) : (
              // Unconfirm
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.transaction_status !== 1 ||
                  !permissions.includes("Confirm Sales Order")
                    ? true
                    : false
                }
                loading={loadingUnconfirm}
                onClick={this.handleModalUnconfirmSO}
              >
                <IntlMessages id="button.unconfirmTransaction" />
              </Button>
            )}

            {/* Close */}
            <Popconfirm
              title={<IntlMessages id="confirmCloseTransaction" />}
              onConfirm={() => this.props.closeSalesOrder(data.id_sales_order)}
              okText={<IntlMessages id="yes" />}
              cancelText={<IntlMessages id="no" />}
            >
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.transaction_status === 3 ||
                  data.transaction_status === 4 ||
                  data.transaction_status === 6 ||
                  data.transaction_status === 5
                    ? true
                    : false
                }
                loading={loadingClose}
              >
                <IntlMessages id="button.closeTransaction" />
              </Button>
            </Popconfirm>

            {/* Cancel */}

            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.transaction_status !== 1 ||
                !permissions.includes("Cancel Sales Order")
                  ? true
                  : false
              }
              loading={loadingCancel}
              onClick={this.handleModalCancelSO}
            >
              <IntlMessages id="button.cancelTransaction" />
            </Button>

            <Row>
              <Col span={24}>
                {this.state.total_exclude_discount_tax - this.state.total_discount < 0 ||
                  this.state.err_discount ? (
                  <IntlMessages id="discountExceed">
                    {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
                  </IntlMessages>
                ) : null}
              </Col>
              <Col span={24}>
                {this.state.err_item ? (
                  <IntlMessages id="itemDataEmpty">
                    {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
                  </IntlMessages>
                ) : null}
              </Col>
            </Row>
          </Form.Item>
        ) : null}

        <Row>
          <Col span={24}>
            {this.state.dataChanged ? (
              <Alert
                message={<IntlMessages id="saveWarning" />}
                type="warning"
                showIcon
              />
            ) : null}
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrappedSalesOrderUpdateForm = Form.create({
  name: "update_sales_order"
})(SalesOrderUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    {
      cancelSalesOrder,
      closeSalesOrder,
      confirmSalesOrder,
      getCustomerContacts,
      getItemsForSO,
      getUpdateSODetails,
      unconfirmSalesOrder
    }
  )
)(WrappedSalesOrderUpdateForm);
